/**
 * # app.js
 *
 * This is the main application entry point for [Core the Loot
 * Hound](http://coretheloothound.com/). This file contains the main App as well
 * as any initializers.
 *
 * ## Initializers
 *
 * * **storage** injects the storage module into all routes and controllers.
 */
window.App = Ember.Application.create({
  LOG_VIEW_LOOKUPS: true,
  LOG_TRANSITIONS: true,
  LOG_TRANSITIONS_INTERNAL: true,
  LOG_ACTIVE_GENERATION: true
});

Ember.Application.initializer({
  name: 'storage',
  initialize: function(container, application){
    application.register('service:storage', window.App.Storage);
    application.inject('route', 'storage', 'service:storage');
    application.inject('controller', 'storage', 'service:storage');
    application.inject('adapter', 'storage', 'service:storage');
  }
});

window.App.ApplicationSerializer = DS.ActiveModelSerializer.extend({
  primaryKey: '_id'
});

window.App.ApplicationAdapter = DS.ActiveModelAdapter.extend({
  host: 'https://api.byfirebepurged.com',

  headers: function() {
    return {
      "Authorization": 'apikey ' + this.get('storage').getValue('apikey')
    };
  }.property().volatile()
});
