window.App.RaidIndexController = Ember.ObjectController.extend({
  battle_tag_name: function() {
    var battle_tag = this.get('creator.battle_tag');
    return battle_tag.substring(0, battle_tag.indexOf('#'));
  }.property('creator'),

  guild_armory_url: function() {
    return 'http://us.battle.net/wow/guild/' +
      this.get('guild.realm') + '/' +
      this.get('guild.name') + '/';
  }.property('guild'),

  totalSignups: function() {
    return this.get('signups').length;
  }.property('signups'),

  totalSlots: function() {
    var groups = this.get('groups');
    return groups.size * groups.number;
  }.property('groups'),

  dateAgo: function() {
    return moment(this.get('date')).fromNow();
  }.property('date'),

  dateCalendar: function() {
    return moment(this.get('date')).calendar();
  }.property('date'),

  allTags: function() {
    return this.get('tags').join(', ');
  }.property('tags'),

  roster: function() {
    var groups = this.get('groups');
    var signups = this.get('signups');
    var result = [];

    for(var i = 1; i <= groups.number; i++) {
      var slot = 1;

      var group = {
        number: i,
        size: groups.size,
        roles: {
          tank: groups.roles.tank,
          healing: groups.roles.healing,
          dps: groups.roles.dps,
          any: (groups.size - groups.roles.tank - groups.roles.healing - groups.roles.dps)
        },
        slots: []
      };

      var needed_roles = {
        tank: groups.roles.tank,
        healing: groups.roles.healing,
        dps: groups.roles.dps
      };

      _.each(['tank', 'healing', 'dps'], function(role) {
        var filled = _.filter(signups, { role: role.toUpperCase() });
        _.each(filled, function(signup) {
          group.slots.push({
            number: slot++,
            role: role.toUpperCase(),
            signup: signup
          });

          needed_roles[role]--;
        });

        if(needed_roles[role] > 0) {
          _.times(needed_roles[role], function() {
            group.slots.push({
              number: slot++,
              role: role.toUpperCase()
            });
          });
        };
      });

      // Now fill out with ANY's
      _.times(groups.size - group.slots.length, function() {
        group.slots.push({
          number: slot++,
          role: 'ANY'
        });
      });

      result.push(group);
    }

    return result;
  }.property('groups', 'signups')
});
